body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#card-app{
  height: 100vh;
}
.a-card {
  width: 100%;
  height: 90vh;
  margin: 5vh 0% 10vh 0%;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  background-color: beige;
  transition: -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
box-shadow: 0px 0px 8px 6px rgba(0,0,0,0.12);
}

.b-card-container {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  background-color: #dd9999;
  transition: -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s; 
}

.b-card-container > div {
  background-color: #f0f0f0;
  width: 100%;
  height: 90vh;
  margin: 0vh 0% 0vh 0%;
}

/* customized index page (Laptop) */

.b-card-container > div > .c-card {
  background-color: #d9d9d9;
}

.b-card-container > div > .c-card:nth-child(1) {
  background-color: #dd9999;
}

#a-card-0 {
  background-color: #fafafa;
  width: 100%;
  height: 100vh;
  margin: 0vh 0% 0vh 0%;
}

#a-card-0 .b-card-container {
  width: 100%;
  margin: 0vh 0% 0vh 0%;
}
.b-card {
  width: 90%;
  height: 90vh;
  margin: 0vh 5% 10vh 5%;
  position: relative;
  overflow: hidden;
  /* border-radius: 10px;
  background-color: #dd9999; */
  transition: -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s; 
}

.c-card{
  background-color: cadetblue;
  height: inherit;
  width: 50%;
  float: left;
}

@media only screen and (max-width: 480px) {
  .c-card {
    width: 100%;
  }
}
#card-container {
  position: relative;
  display: block;
  width: 100vw;
  padding: 0;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  margin: 0 auto;
  overflow: hidden;
  transition: -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s;
  transition: transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s, -webkit-transform 1200ms cubic-bezier(0.77, 0, 0.175, 1) 0s; 
}
